.splash {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  .splash-container {
    display: flex;
    flex-direction: column;
    width: 100%;

    .splash-upper {
      display: flex;
      flex-direction: row;
      width: 100%;
      flex: 1;

      .splash-left-profile {
        width: 50%;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
      .splash-right-menu {
        width: 50%;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
    }
    .splash-lower-bar {
      width: 100%;
    }
  }
}